.login {
  position: relative;
  top: 50%;
}

.loginButton {
  background: #865dff;
  padding: 1rem;
  outline: none;
  border: none;
  color: white;
  border-radius: 5px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.25);
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
